$values:15 28 38;

@each $v in $values{
    .m-#{$v}{
        margin: #{$v}px;
    }
    .my-#{$v}{
        margin-top: #{$v}px;
        margin-bottom: #{$v}px;
    }
    .mx-#{$v}{
        margin-left: #{$v}px;
        margin-right: #{$v}px;
    }
    .mt-#{$v}{
        margin-top: #{$v}px;
    }
    .mb-#{$v}{
        margin-bottom: #{$v}px;
    }
    .mr-#{$v}{
        margin-right: #{$v}px;
    }
    .ml-#{$v}{
        margin-left: #{$v}px;
    }
}